/* HomePage.css */

/* ×¢×™×¦×•×‘ ××–×•×¨ ×”×’×™×‘×•×¨ (Hero) */
.hero {
  height: 80vh;
  min-height: 500px;
  background-image: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)), url('../../public/images/placeholder.jpg');
  background-size: cover;
  background-position: center;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: var(--white);
  position: relative;
}

.hero-content {
  max-width: var(--max-width-md);
  padding: var(--spacing-lg);
  z-index: 1;
}

.hero h1 {
  font-size: var(--font-size-5xl);
  margin-bottom: var(--spacing-md);
  color: var(--white);
  font-weight: var(--font-weight-black);
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}

.hero h2 {
  font-size: var(--font-size-2xl);
  margin-bottom: var(--spacing-lg);
  color: var(--white);
  font-weight: var(--font-weight-medium);
  text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.5);
}

.hero h2::after {
  display: none;
}

.hero-subtitle {
  font-size: var(--font-size-lg);
  margin-bottom: var(--spacing-xl);
  font-weight: var(--font-weight-normal);
  max-width: 600px;
  margin-right: auto;
  margin-left: auto;
  line-height: var(--line-height-loose);
}

.hero .btn-primary {
  padding: var(--spacing-md) var(--spacing-xl);
  font-size: var(--font-size-md);
  font-weight: var(--font-weight-bold);
}

/* ×¢×™×¦×•×‘ ××–×•×¨ ×”×ž×‘×•× */
.intro {
  background-color: var(--white);
  padding: var(--spacing-3xl) 0;
}

.intro h2 {
  text-align: center;
  margin-bottom: var(--spacing-xl);
}

.intro-content {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: var(--spacing-2xl);
}

.intro-text {
  flex: 3;
}

.intro-text p {
  font-size: var(--font-size-md);
  line-height: var(--line-height-loose);
  margin-bottom: var(--spacing-lg);
}

.intro-image {
  flex: 2;
  position: relative;
}

.intro-image img {
  width: 100%;
  border-radius: var(--border-radius-lg);
  box-shadow: var(--shadow-lg);
}

.image-caption {
  text-align: center;
  margin-top: var(--spacing-sm);
  font-size: var(--font-size-sm);
  color: var(--text-light);
}

/* ×¢×™×¦×•×‘ ××–×•×¨ ×ž×©×•×ª×£ ×©×œ ×ž×¡×¢ ×•×¤×¨×§×™ ×—×™×™× */
.journey-chapters-section {
  padding: var(--spacing-3xl) 0;
  background-color: var(--background-dark);
}

.section-title {
  text-align: center;
  margin-bottom: var(--spacing-2xl);
  color: var(--text-color);
  font-size: var(--font-size-2xl);
}

.journey-chapters-container {
  display: flex;
  gap: var(--spacing-xl);
}

/* ×¢×™×¦×•×‘ ×¢×ž×•×“×ª ×¦×™×¨ ×”×–×ž×Ÿ (×™×ž×™×Ÿ) */
.timeline-column {
  flex: 1;
  order: 1; /* ×¢×ž×•×“×ª ×¦×™×¨ ×”×–×ž×Ÿ ×ª×•×¤×™×¢ ×‘×¦×“ ×™×ž×™×Ÿ */
}

.timeline-full-link-container {
  text-align: center;
  margin-top: var(--spacing-xl);
}

/* ×¢×™×¦×•×‘ ×œ×¦×™×¨ ×”×–×ž×Ÿ ×”×ž×¡×•× ×›×¨×Ÿ */
.synchronized-timeline {
  position: relative;
}

/* ×ª××¨×™×›×™× ×¢×‘×¨×™×™× ×•×œ×•×¢×–×™×™× */
.timeline-date-wrapper {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-xs);
  line-height: 1.3;
}

.timeline-gregorian-date {
  font-weight: var(--font-weight-medium);
}

.timeline-hebrew-date {
  font-size: 0.9em;
  opacity: 0.9;
}

/* ×ž×¨×•×•×— × ×•×¡×£ ×œ×ª×™×‘×•×ª ×”×ª×•×›×Ÿ ×‘×“×£ ×”×‘×™×ª */
.synchronized-timeline .vertical-timeline-element-content {
  padding-top: 20px !important;
  margin-top: 15px !important;
}

/* ×¢×™×¦×•×‘ ×¢×ž×•×“×ª ×¤×¨×§×™ ×”×—×™×™× (×©×ž××œ) */
.chapters-column {
  flex: 1;
  order: 2; /* ×¢×ž×•×“×ª ×¤×¨×§×™ ×”×—×™×™× ×ª×•×¤×™×¢ ×‘×¦×“ ×©×ž××œ */
  position: relative;
}

.chapters-container {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-3xl);
  position: relative;
}

/* ×¢×™×¦×•×‘ ×§×•×‘×™×•×ª ×¤×¨×§×™ ×”×—×™×™× */
.chapter-card {
  position: relative;
  margin-bottom: var(--spacing-xl);
}

.chapter-card:not(:last-child)::after {
  content: '';
  position: absolute;
  top: 100%;
  right: 40px;
  width: 2px;
  height: calc(var(--spacing-3xl) - 10px);
  background-color: var(--gray-light);
  z-index: 0;
}

.chapter-card-content {
  display: flex;
  background-color: transparent;
  border-radius: var(--border-radius-md);
  overflow: hidden;
  transition: all var(--transition-normal) ease;
}

.chapter-image-container {
  width: 80px;
  height: 80px;
  flex-shrink: 0;
  overflow: hidden;
  border-radius: var(--border-radius-md);
  box-shadow: var(--shadow-sm);
}

.chapter-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform var(--transition-normal) ease;
}

.chapter-card:hover .chapter-image {
  transform: scale(1.05);
}

.chapter-details {
  padding: var(--spacing-sm) var(--spacing-md);
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.chapter-title {
  margin: 0 0 var(--spacing-xs) 0;
  font-size: var(--font-size-md);
  color: var(--primary-color);
}

.chapter-date-badge {
  display: inline-block;
  background-color: var(--primary-color);
  color: var(--white);
  padding: 4px 12px;
  border-radius: 15px;
  font-size: var(--font-size-sm);
  margin-bottom: var(--spacing-sm);
  line-height: 1.4;
  box-shadow: var(--shadow-sm);
}

.hebrew-year {
  display: inline-block;
  font-weight: var(--font-weight-normal);
  opacity: 0.9;
}

.chapter-summary {
  margin-bottom: var(--spacing-md);
  font-size: var(--font-size-sm);
  line-height: var(--line-height-normal);
  color: var(--text-color);
}

.chapter-link {
  align-self: flex-start;
  color: var(--primary-color);
  font-size: var(--font-size-sm);
  font-weight: var(--font-weight-medium);
  text-decoration: none;
  transition: color var(--transition-fast) ease;
}

.chapter-link:hover {
  color: var(--primary-dark);
  text-decoration: underline;
}

.chapter-link::after {
  content: ' â†’';
}

/* ×¢×™×¦×•×‘ ××–×•×¨ ×”×¦×™×˜×•×˜×™× */
.quotes-section {
  background-color: var(--primary-color);
  padding: var(--spacing-3xl) 0;
  color: var(--white);
}

.quotes-section h2 {
  text-align: center;
  margin-bottom: var(--spacing-xl);
  color: var(--white);
}

.quotes-section h2::after {
  background-color: var(--white);
  right: 50%;
  transform: translateX(50%);
}

.quotes-container {
  display: flex;
  flex-wrap: wrap;
  gap: var(--spacing-xl);
  justify-content: center;
}

/* ×¢×™×¦×•×‘ ×ª×¦×•×’×ª ×’×œ×¨×™×” ×ž×§×“×™×ž×” */
.gallery-preview {
  background-color: var(--white);
  padding: var(--spacing-3xl) 0;
}

.gallery-preview h2 {
  text-align: center;
  margin-bottom: var(--spacing-xl);
}

.gallery-preview-link {
  text-align: center;
  margin-top: var(--spacing-xl);
}

/* ×¢×™×¦×•×‘ ××–×•×¨ ×§×¨×™××” ×œ×¤×¢×•×œ×” (CTA) */
.cta-section {
  background-color: var(--secondary-color);
  padding: var(--spacing-3xl) 0;
  text-align: center;
}

.cta-section h2 {
  color: var(--text-color);
  margin-bottom: var(--spacing-md);
}

.cta-section h2::after {
  background-color: var(--white);
  right: 50%;
  transform: translateX(50%);
}

.cta-section p {
  font-size: var(--font-size-lg);
  max-width: 600px;
  margin-right: auto;
  margin-left: auto;
  margin-bottom: var(--spacing-xl);
}

.cta-buttons {
  display: flex;
  justify-content: center;
  gap: var(--spacing-md);
}

/* ×”×ª××ž×•×ª ×œ×ž×•×‘×™×™×œ ×¢×‘×•×¨ ×¦×™×¨ ×”×–×ž×Ÿ ×‘×“×£ ×”×‘×™×ª */
@media (max-width: 1169px) {
  .timeline-preview .vertical-timeline-element .vertical-timeline-element-date {
    right: 0 !important;
    left: auto !important;
    top: -22px !important;
    font-size: 10px !important;
    padding: 2px 8px !important;
    max-width: 80px !important;
    white-space: nowrap !important;
    overflow: hidden !important;
    text-overflow: ellipsis !important;
  }
}

/* ×ª×¦×•×’×•×ª ×ž×“×™×” (×¨×¡×¤×•× ×¡×™×‘×™×•×ª) */
@media (max-width: 1200px) {
  .journey-chapters-container {
    gap: var(--spacing-lg);
  }
}

@media (max-width: 992px) {
  .journey-chapters-container {
    flex-direction: column;
  }

  .timeline-column,
  .chapters-column {
    width: 100%;
  }

  .timeline-column {
    margin-bottom: var(--spacing-3xl);
  }

  .chapters-container {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    gap: var(--spacing-xl);
  }

  .chapter-card:not(:last-child)::after {
    display: none;
  }
}

@media (max-width: 768px) {
  .hero h1 {
    font-size: var(--font-size-4xl);
  }

  .hero h2 {
    font-size: var(--font-size-xl);
  }

  .intro-content {
    flex-direction: column;
  }

  .intro-image {
    margin-top: var(--spacing-xl);
    max-width: 400px;
  }

  .journey-chapters-container {
    gap: var(--spacing-2xl);
  }

  .chapters-container {
    grid-template-columns: 1fr;
  }

  .cta-buttons {
    flex-direction: column;
    align-items: center;
    gap: var(--spacing-md);
  }

  .synchronized-timeline .vertical-timeline-element .vertical-timeline-element-date {
    font-size: 9px !important;
    padding: 2px 6px !important;
    max-width: 70px !important;
  }
}

@media (max-width: 640px) {
  .hero {
    height: 60vh;
  }

  .hero h1 {
    font-size: var(--font-size-3xl);
  }

  .hero h2 {
    font-size: var(--font-size-lg);
  }

  .hero-subtitle {
    font-size: var(--font-size-base);
  }

  .quotes-container {
    flex-direction: column;
  }

  .timeline-column {
    padding: var(--spacing-md);
  }

  .chapter-image-container {
    width: 60px;
    height: 60px;
  }

  .chapter-summary {
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
  }
}
